.footer__container {
    background-color: var(--footer-bg);
    color: var(--footer-text);
    border-top-right-radius: var(--border-radius-lg);
    font-family: 'Ubuntu', sans-serif;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
}

.footer__logo {
    margin: 70px 0 20px 0;
    width: 100px;
    height: auto;
}

.footer__list {
    list-style-type: none;
    padding: 10px 0;
    font-weight: 700;
}

.footer__link {
    font-weight: 400;
    padding: 5px 0;
}

.footer__link:nth-of-type(1) {
    margin-top: 20px;
}

.footer__link:hover {
    text-decoration: underline;
    cursor: pointer;
}

@media (min-width: 768px) {
  .footer__container {
    flex-flow: row wrap;
    align-items: flex-start;
    justify-content: flex-start;
    padding-left: 10.3rem;
    padding-bottom: 70px;
    text-align: left;
  }   

  .footer__container > img, ul {
    margin-top: 70px;
  }

  .footer__links {
    display: flex;
    width: 65%;
    justify-content: space-around;
    padding-left: 5.5rem;
  }

  .footer__list {
    color: var(--white);
    font-weight: 400;
  }

  .footer__link {
    color: var(--footer-text);
  }
}